.firstRectangle {
  position: absolute;
  width: 37%;
  height: 320px;
  left: 15%;
  top: 15%;
  background: #ffffff;
  border: 0.5px solid #020202;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 0px 5px 20px 0px #5a5a5a;
}

